<template>
    <v-app>
        <v-app-bar app dark color="primary">
            <div class="d-flex align-center">
                <v-img
                alt="Vuetify Logo"
                class="shrink mr-2"
                contain
                :src="`${publicPath}`+'img/icons/icon-152x152.png'"
                transition="scale-transition"
                width="40"
                />
                Cultist Simulator Translation Tool
            </div>
            <v-spacer></v-spacer>
            <v-btn text @click="howToUseDialog = true">Help</v-btn>
        </v-app-bar>

        <v-content>
            <v-container>
                <!-- File inputs -->
                <v-row wrapped>
                    <v-col cols="4" class="text-center">
                        New Version Files
                        <v-file-input multiple label="New Version File" outlined dense @change="loadFiles($event, 'newVersionFiles')"></v-file-input>
                    </v-col>
                    <v-col cols="4" class="text-center">
                        Current Version English core Files
                        <v-file-input multiple label="Current Version File" outlined dense @change="loadFiles($event, 'currentVersionFiles')"></v-file-input>
                    </v-col>
                    <v-col cols="4" class="text-center">
                        Current Version Translated core Files
                        <v-file-input multiple label="Current Translation File" outlined dense @change="loadFiles($event, 'currentVersionTranslatedFiles')"></v-file-input>
                        <div class="text-center"><v-btn color="primary" @click="downloadTranslationZip"><v-icon>mdi-download</v-icon></v-btn></div>
                    </v-col>
                </v-row>
                <!-- Differences table -->
                <v-row wrapped>
                    <v-col cols="12">
                        <v-simple-table>
                        <template v-slot:default>
                            <thead>
                                <tr>
                                    <th>File name</th>
                                    <th>New version file</th>
                                    <th>Current version translated file differences</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="file in currentVersionFiles" :key="file.name" @click="openTranslationDialog(file)" style="cursor: pointer">
                                    <td>{{file.name}}</td>
                                    <td></td>

                                    <td v-if="untranslatedEntries[file.name].newFile">New file, doesn't exist in translated core</td>
                                    <td v-else :class="computeFileColor(file)">{{getElements(file.obj).length-untranslatedEntries[file.name].untranslated.length}}/{{getElements(file.obj).length}}</td>
                                </tr>
                            </tbody>
                        </template>
                    </v-simple-table>
                </v-col>
                </v-row>
            </v-container>
            <!-- Translation Dialog -->
            <v-dialog v-model="translationDialog" height="900">
                <v-card>
                    <v-card-title v-if="fileToTranslate">{{fileToTranslate.name}}</v-card-title>
                    <v-card-text>
                        <v-container v-if="fileToTranslate">
                            <v-row wrapped>
                                <v-col cols="3" class="dialogScrollContainer">
                                    <v-list-item
                                        v-for="elementId in getElements(currentVersionFiles.find(f => f.name === fileToTranslate.name).obj).map(e => e.id).sort()" :key="elementId"
                                        @click="showElement(elementId)"
                                        :class="{selectedElement: elementId === displayedElementId}"
                                    >
                                        <v-list-item-content>
                                            <v-list-item-title :class="computeTranslationColor(elementId)">{{elementId}}</v-list-item-title>
                                        </v-list-item-content>
                                    </v-list-item>
                                </v-col>
                                <!-- Original text panel -->
                                <v-col v-if="displayedElementId" class="dialogScrollContainer">
                                    <TranslationPanel title="Original Text" :element="elementToTranslate" :readonly="true"/>
                                </v-col>
                                <!-- Translated text panel -->
                                <v-col v-if="displayedElementId" class="dialogScrollContainer">
                                    <TranslationPanel title="Translated Content" :element="translatedElement" :original-element="elementToTranslate" :translation-map="translationMap"/>
                                </v-col>
                            </v-row>
                        </v-container>
                    </v-card-text>
                </v-card>
            </v-dialog>

            <!-- How To Use Dialog -->
            <HowToUseDialog v-model="howToUseDialog"/>

        </v-content>
        <v-footer app>
            <div class="caption">
                Cultist Simulator Translation Tool is an unofficial tool for Cultist Simulator. All rights, data,   & pictures used here belong to Weather Factory.
            </div>
            <v-spacer></v-spacer>
            <div class="caption">Made with <v-icon small color="primary">mdi-heart</v-icon> by Genroa</div>
        </v-footer>
    </v-app>
</template>

<script>
/* eslint no-eval: 0 */
import Jszip from 'jszip'
import { saveAs } from 'file-saver'
import TranslationPanel from '@/components/TranslationPanel'
import HowToUseDialog from '@/components/HowToUseDialog'

export default {
	name: 'App',

	components: {
		HowToUseDialog,
		TranslationPanel
	},

	data: () => ({
		publicPath: process.env.BASE_URL,
		newVersionFiles: [],
		currentVersionFiles: [],
		currentVersionTranslatedFiles: [],
		translationDialog: false,
		fileToTranslate: null,
		translatedFile: null,
		displayedElementId: null,
		howToUseDialog: false
	}),

	computed: {
		untranslatedEntries () {
			const untranslatedEntries = this.buildEntryForEachFile(this.currentVersionFiles, () => ({ newFile: false, newEntries: [], untranslated: [] }))
			for (const file of this.currentVersionFiles) {
				const translatedFile = this.currentVersionTranslatedFiles.find(f => f.name === file.name)
				if (!translatedFile) {
					untranslatedEntries[file.name].newFile = true
					continue
				}
				for (const element of this.getElements(file.obj)) {
					const translatedElement = this.getElements(translatedFile.obj).find(e => e.id === element.id)
					if (!translatedElement) {
						untranslatedEntries[file.name].newEntries.push(element.id)
						continue
					}
					// Are objects similar, or is there a text that differs?
					if (
						translatedElement.startdescription !== element.startdescription ||
						translatedElement.description !== element.description ||
                        translatedElement.label !== element.label
					) continue

					// If their texte is identical, maybe their slots are different?
					// No slot.
					if (!element.slots) {
						untranslatedEntries[file.name].untranslated.push(element.id)
						continue
					}

					for (let i = 0; i < element.slots.length; i++) {
						// Translated slot
						if (element.slots[i].label !== translatedElement.slots[i].label ||
                        element.slots[i].description !== translatedElement.slots[i].description) {
							continue
						} else {
							// Untranslated slot
							untranslatedEntries[file.name].untranslated.push(element.id)
							break
						}
					}
				}
			}
			return untranslatedEntries
		},

		elementToTranslate () {
			if (!this.displayedElementId || !this.fileToTranslate) return null
			return this.getElements(this.fileToTranslate.obj).find(el => el.id === this.displayedElementId)
		},

		translatedElement () {
			if (!this.displayedElementId || !this.translatedFile) return null
			return this.getElements(this.translatedFile.obj).find(el => el.id === this.displayedElementId)
		},

		translationMap () {
			const map = {}
			for (const file of this.currentVersionFiles) {
				const translatedFile = this.currentVersionTranslatedFiles.find(f => f.name === file.name)
				if (!translatedFile) continue

				for (const element of this.getElements(file.obj)) {
					const translatedElement = this.getElements(translatedFile.obj).find(e => e.id === element.id)
					if (!translatedElement) continue

					// Are objects similar, or is there a text that differs?
					if (translatedElement.startdescription !== element.startdescription) {
						map[element.startdescription] = translatedElement.startdescription
					}
					if (translatedElement.description !== element.description) {
						map[element.description] = translatedElement.description
					}
					if (translatedElement.label !== element.label) {
						map[element.label] = translatedElement.label
					}

					// If their texte is identical, maybe their slots are different?
					// No slot.
					if (!element.slots) continue

					for (let i = 0; i < element.slots.length; i++) {
						// Translated slot
						if (element.slots[i].label !== translatedElement.slots[i].label) {
							map[element.slots[i].label] = translatedElement.slots[i].label
						}
						if (element.slots[i].description !== translatedElement.slots[i].description) {
							map[element.slots[i].description] = translatedElement.slots[i].description
						}
					}
				}
			}
			return map
		}
	},

	methods: {
		getElements (obj) {
			return obj.decks ??
            obj.elements ??
            obj.endings ??
            obj.legacies ??
            obj.recipes ??
            obj.verbs
		},

		buildEntryForEachFile (files, objBuilder) {
			return Object.fromEntries(
				files.map(f => ([
					f.name,
					objBuilder()
				]))
			)
		},

		loadFiles (files, filesArray) {
			this[filesArray] = []
			for (const f of files)	{
				const fr = new FileReader()
				fr.onload = e => {
					const obj = JSON.parse(e.target.result)
					this[filesArray].push({ name: f.name, obj })
				}
				fr.readAsText(f)
			}
		},

		openTranslationDialog (f) {
			this.fileToTranslate = f
			this.translatedFile = this.currentVersionTranslatedFiles.find(tf => tf.name === f.name)
			this.translationDialog = true
		},

		showElement (elementId) {
			this.displayedElementId = elementId
		},

		computeTranslationColor (elementId) {
			const untranslated = this.untranslatedEntries[this.fileToTranslate.name].untranslated.includes(elementId)
			return {
				untranslated,
				translated: !untranslated
			}
		},

		computeFileColor (file) {
			const noTranslated = this.untranslatedEntries[file.name].untranslated.length === this.getElements(file.obj).length
			const anyUntranslated = this.untranslatedEntries[file.name].untranslated.length > 0
			return {
				untranslated: noTranslated,
				translating: !noTranslated && anyUntranslated,
				translated: !anyUntranslated
			}
		},

		downloadTranslationZip () {
			var zip = new Jszip()
			for (const file of this.currentVersionTranslatedFiles) {
				zip.file(file.name, JSON.stringify(file.obj, undefined, 2))
			}
			zip.generateAsync({ type: 'blob' })
				.then(function (content) {
					saveAs(content, 'translations.zip')
				})
		}
	}
}
</script>
<style>
.translated {
    color: green;
}

.untranslated {
    color: red;
}

.translating {
    color: orange;
}

.selectedElement {
    background-color: #e8e8e8;
}

.dialogScrollContainer {
    overflow-y: auto;
    height: 75vh;
}
</style>
