<template>
    <v-container>
        <v-row wrapped>
            <v-col cols="12" class="mb-2">{{title}}</v-col>

            <!-- Label -->
            <v-col :cols="readonly ? 12 : 11" class="py-0" v-if="element.label !== undefined">
                <v-textarea :readonly="readonly" dense label="Label" outlined no-resize rows="2" v-model="element.label"/>
            </v-col>
            <v-col cols="1" v-if="!readonly && element.label !== undefined">
                <PreviousTranslationButton :translation-map="translationMap" :translation-key="originalElement.label" @click="element.label = $event"/>
            </v-col>

            <!-- Start Description -->
            <v-col :cols="readonly ? 12 : 11" class="py-0" v-if="element.startdescription !== undefined">
                <v-textarea :readonly="readonly" dense label="Start Description" outlined no-resize rows="4" v-model="element.startdescription"/>
            </v-col>
            <v-col cols="1" v-if="!readonly && element.startdescription !== undefined">
                <PreviousTranslationButton :translation-map="translationMap" :translation-key="originalElement.startdescription" @click="element.startdescription = $event"/>
            </v-col>

            <!-- Description -->
            <v-col :cols="readonly ? 12 : 11" class="py-0" v-if="element.description !== undefined">
                <v-textarea :readonly="readonly" dense label="Description" outlined no-resize rows="4" v-model="element.description"/>
            </v-col>
            <v-col cols="1" v-if="!readonly && element.description !== undefined">
                <PreviousTranslationButton :translation-map="translationMap" :translation-key="originalElement.description" @click="element.description = $event"/>
            </v-col>

            <template v-if="element.slots">
                <v-col cols="12" v-for="(slot, i) in element.slots" :key="i" class="py-0">
                    <v-container class="py-0">
                        <v-row wrapped>
                            <v-col cols="12" class="mb-2 py-0">Slot {{i+1}}</v-col>
                            <v-col :cols="readonly ? 12 : 11" class="py-0">
                                <v-text-field v-if="slot.label" :readonly="readonly" dense outlined label="Label" v-model="slot.label" />
                            </v-col>
                            <v-col cols="1" v-if="!readonly && slot.label !== undefined">
                                <PreviousTranslationButton :translation-map="translationMap" :translation-key="originalElement.slots[i].label" @click="slot.label = $event"/>
                            </v-col>

                            <v-col :cols="readonly ? 12 :11" class="py-0">
                                <v-text-field v-if="slot.description" :readonly="readonly" dense outlined label="Description" v-model="slot.description"/>
                            </v-col>
                            <v-col cols="1" v-if="!readonly && slot.description !== undefined">
                                <PreviousTranslationButton :translation-map="translationMap" :translation-key="originalElement.slots[i].description" @click="slot.description = $event"/>
                            </v-col>
                        </v-row>
                    </v-container>
                </v-col>
            </template>
        </v-row>
    </v-container>
</template>
<script>
import PreviousTranslationButton from '@/components/PreviousTranslationButton'
export default {
	components: {
		PreviousTranslationButton
	},
	props: ['title', 'element', 'readonly', 'originalElement', 'translationMap']
}
</script>
